import { Component, OnInit } from '@angular/core';
import { Http ,Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-resourceform',
  templateUrl: './resourceform.component.html',
  styleUrls: ['./resourceform.component.css']
})
export class ResourceformComponent implements OnInit {

  private _options = { headers: new Headers({ 'Content-Type': 'application/json' }) };

    res_name:String;
    res_displayname:String;
    restype_id:String;
    res_description:String;

    restypes = [];

    eid: String;

    resos = [];

  constructor( private _http:Http) { 
    this.loadResourceTypes();
    this.loadGeneratedResources();
  }

  ngOnInit() {
    document.getElementById('update-btn').style.display = 'none';
  }

  loadResourceTypes(){
    this._http.get('/resources/res_types')
      .map(res => res.json())
      .subscribe(
        restypedata => this.restypes = restypedata,
        err => console.log("error Occured while fetching Resource Type" , err)
      );
  }

  loadGeneratedResources(){
    this._http.get('/resources/generated_resources')
      .map(resp => resp.json())
      .subscribe(
        resdata => this.resos = resdata,
        err => console.log("error Occured while fetching Resource Type" , err)
      );
  }

  onGenerateResource(){
    const resource = {
      restype_id      :this.restype_id,
      res_name        :this.res_name,
      res_displayname :this.res_displayname,
      res_description :this.res_description
    }
    if (resource.res_name == undefined || resource.res_displayname == undefined || resource.restype_id == undefined || resource.res_description == undefined )
    { return false; }
    else 
    { 
      this._http.post('/resources/addResource',JSON.stringify(resource), this._options)
        .subscribe(
          res => { if ( res.status = 200 ){alert('Resource Created sucessfully'); this.loadGeneratedResources(); }  } ,
          err => { alert( 'Opps smthing went wrong !' ); }
        )
      this.restype_id = this.res_name = this.res_displayname = this.res_description = null ;       
    }
  }

  OnEditResource(id){
    
    if (id == undefined || id == null) { alert("opps smthing went wrong !"); }
    else
    {
      this._http.get('/resources/ResourceById/?id='+id )
        .subscribe(
          res => { 
            if ( res.status = 200 )
              {
                var eresobj=res.json();
                this.res_name = eresobj.res_name;
                this.res_displayname = eresobj.res_displayname;
                this.res_description = eresobj.res_description;
                this.restype_id = eresobj.restype_id;
                this.eid = eresobj._id;
              }
          } ,
          err => { alert( 'Opps smthing went wrong !' ); }
        )
        document.getElementById('submit-btn').style.display = 'none';
        document.getElementById('update-btn').style.display = 'block';
    }
  }

  OnUpdateResource(id){
    const editedresource = {
      _id : this.eid,
      restype_id      :this.restype_id,
      res_name        :this.res_name,
      res_displayname :this.res_displayname,
      res_description :this.res_description
    }
    if (editedresource.res_name == undefined || editedresource.res_displayname == undefined || editedresource.restype_id == undefined || editedresource.res_description == undefined )
    { return false; }
    else 
    { 
      this._http.put('/resources/updateResource',editedresource, this._options)
        .subscribe(
          res => { if ( res.status = 200 ){alert('Resource updated sucessfully') ;this.loadGeneratedResources(); }  } ,
          err => { alert( 'Opps smthing went wrong !' ); }
        )
    }
  }

  OnCancel(){
    this.restype_id = this.res_name = this.res_displayname = this.res_description = this.eid = null ;
    document.getElementById('submit-btn').style.display = 'block';
    document.getElementById('update-btn').style.display = 'none';
  }

  OnDelResource(id){
    var data = { "id" : `${id}` } ;
    var _deloptions = { headers: new Headers({ 'Content-Type': 'application/json' }) , body: data };
    if (id == undefined || id == null)
    {
      alert("opps smthing went wrong !");
    }
    else
    {
      this._http.delete('/resources/delResource', _deloptions )
        .subscribe(
          res => { if ( res.status = 200 ){alert('Resource Deleted sucessfully'); this.loadGeneratedResources();}  } ,
          err => { alert( 'Opps smthing went wrong !' ); }
        )
    }
  }

}



